import "../styles/students.css"
import Navbar from "./parts/navbar"
import React from "react";

import { useState } from "react";
import video from "../bilder/video.mp4"

export default function Students(){
    const [login,setlogin]=useState(false);

    const [click_vid, setclick_vid] = useState(false)



    return <div>
        
        <Navbar login={login} setlogin={setlogin}/>
        <div className="students-nav-div">
            <div className="students-nav" onClick={()=>{window.scroll(0,410)}}><p className="students-nav-text">Introduction</p></div>
            <div className="students-nav" onClick={()=>{window.scroll(0,800)}}><p className="students-nav-text">Free and premium</p></div>
            <div className="students-nav" onClick={()=>{window.scroll(0,1150)}}><p className="students-nav-text">Nettuni courses</p></div>
            <div className="students-nav" onClick={()=>{window.scroll(0,1600)}}><p className="students-nav-text">Finding the right course</p></div>
            <div className="students-nav" onClick={()=>{window.scroll(0,2050)}}><p className="students-nav-text">Course Structre</p></div>
            <div className="students-nav" onClick={()=>{window.scroll(0,2400)}}><p className="students-nav-text">Problems and solutions</p></div>
        </div>
        <div className="students-intro">
            <div className="intro-div">

                <div className="student-vid-div">
                    <div className="spill" onClick={()=>{setclick_vid(true)}} style={click_vid ? {display:"none"}:{display:"flex"}}><p className="video-tittel">Introduction</p></div>
                    <video controls className={click_vid ? "minviduten" : "minvid"}>
                        <source type="video/mp4" src={video} className="videoen"/> 
                    </video>
                </div>

                <div className="intro-svg">
                    <div className="student1">
                        <div className="student-svg-div">
                            <div className="student-svg">
                                <svg className="svg-ene" viewBox="0 0 80 144" fill="none">
                                    <line x1="41" x2="41" y2="57" stroke="#C6E7F1" stroke-width="6"/>
                                    <line x1="41" y1="86" x2="41" y2="144" stroke="#C6E7F1" stroke-width="6"/>
                                    <circle cx="40.5" cy="72.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                                </svg>
                            </div>
                            <div className="student-svg-text-div"><p className="student-svg-text">Navigate the course structure</p></div>
                        </div>
                    </div>
                    <div className="student2">
                        <div className="student-svg-div">
                            <div className="student-svg">
                                <svg className="svg-ene" viewBox="0 0 80 144" fill="none">
                                    <line x1="41" x2="41" y2="57" stroke="#C6E7F1" stroke-width="6"/>
                                    <line x1="41" y1="86" x2="41" y2="144" stroke="#C6E7F1" stroke-width="6"/>
                                    <circle cx="40.5" cy="72.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                                </svg>
                            </div>
                            <div className="student-svg-text-div"><p className="student-svg-text">Watch and understand the content</p></div>
                        </div>
                    </div>
                    <div className="student3">
                        <div className="student-svg-div">
                            <div className="student-svg">
                                <svg className="svg-ene" viewBox="0 0 80 144" fill="none">
                                    <line x1="41" x2="41" y2="57" stroke="#C6E7F1" stroke-width="6"/>
                                    <line x1="41" y1="86" x2="41" y2="144" stroke="#C6E7F1" stroke-width="6"/>
                                    <circle cx="40.5" cy="72.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                                </svg>
                            </div>
                            <div className="student-svg-text-div"><p className="student-svg-text">Test and expand your knowledge</p></div>
                        </div>
                    </div>
                </div>
            
            </div>
        </div>

        <div className="use-nettuni">

            <div className="use-1">

                <div className="use-header">
                    <h2 className="header-text">Free and Premium</h2>
                </div>

                <div className="use-para">
                    <div className="use-para-svg">
                        <svg className="svg-to" viewBox="0 0 45 301" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <g clip-path="url(#clip0_1_2)">
                            <line x1="23" x2="23" y2="135" stroke="#C6E7F1" stroke-width="6"/>
                            <line x1="22.9997" y1="163.956" x2="24.9997" y2="300.956" stroke="#C6E7F1" stroke-width="6"/>
                            <circle cx="22.5" cy="150.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                            </g>
                            <defs>
                            <clipPath id="clip0_1_2">
                            <rect width="45" height="301" fill="white"/>
                            </clipPath>
                            </defs>
                        </svg>

                    </div>
                    <div className="paraen">
                        <p className="paragraph">
                        Nettuni offers two distinct tiers: Free and Premium. Users can easily elevate their experience by upgrading to the Premium Tier for just $4 per month upon<u className="under-text">signing up</u>. Premium users enjoy unrestricted access to all Nettuni courses. Even for users who choose not to sign up, access to Free Tier courses remains available</p>
                    </div>
                </div>
            </div>


            <div className="use-2">

                <div className="use-header">
                    <h2 className="header-text">Nettuni Courses</h2>
                </div>

                <div className="use-para">
                    <div className="use-para-svg">
                        <svg className="svg-to" viewBox="0 0 45 301" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <g clip-path="url(#clip0_1_2)">
                            <line x1="23" x2="23" y2="135" stroke="#C6E7F1" stroke-width="6"/>
                            <line x1="22.9997" y1="163.956" x2="24.9997" y2="300.956" stroke="#C6E7F1" stroke-width="6"/>
                            <circle cx="22.5" cy="150.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                            </g>
                            <defs>
                            <clipPath id="clip0_1_2">
                            <rect width="45" height="301" fill="white"/>
                            </clipPath>
                            </defs>
                        </svg>
                    </div>

                    <div className="paraen">
                        <p className="paragraph">
                            Nettuni is crafted to offer users a vibrant learning experience in STEM (Science, Technology, Engineering, and Mathematics) subjects. The course structure of Nettuni is tailored to predominantly emphasize problem-solving disciplines, encompassing mathematics, physics, chemistry, computer science, information technology, engineering, and biology.
                        </p>
                    </div>
                </div>
            </div>

            <div className="use-3">

                <div className="use-header">
                    <h2 className="header-text">Finding the right course</h2>
                </div>

                <div className="use-para">

                    <div className="use-para-svg">
                        <svg className="svg-to" viewBox="0 0 45 301" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <g clip-path="url(#clip0_1_2)">
                            <line x1="23" x2="23" y2="135" stroke="#C6E7F1" stroke-width="6"/>
                            <line x1="22.9997" y1="163.956" x2="24.9997" y2="300.956" stroke="#C6E7F1" stroke-width="6"/>
                            <circle cx="22.5" cy="150.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                            </g>
                            <defs>
                            <clipPath id="clip0_1_2">
                            <rect width="45" height="301" fill="white"/>
                            </clipPath>
                            </defs>
                        </svg>
                    </div>

                    <div className="paraen">
                        <p className="paragraph">
                            While all Nettuni courses are meticulously designed to cater to diverse user needs, it's important to acknowledge that users may not always be at the same level of proficiency. To address this, each course provides clear level specifications.
                            <br/>
                            The High School level is tailored for users in high school or those at a beginner stage, while the University level caters to students in university or individuals with some knowledge in the respective field. The All Level designation indicates that the course is suitable for users of all backgrounds, requiring no prior knowledge.
                            <br/>
                            Furthermore, each course page outlines specific prerequisites, and many courses follow a designated book, enhancing the structure of notation and the overall learning experience. When selecting a course, consider factors such as the course level, prerequisites, and the reference book to ensure the best fit for your needs.
                        </p>
                    </div>
                </div>
            </div>

            <div className="use-4">

                <div className="use-header">
                    <h2 className="header-text">Course Structre</h2>
                </div>

                <div className="use-para">
                    <div className="use-para-svg">
                        <svg className="svg-to" viewBox="0 0 45 301" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <g clip-path="url(#clip0_1_2)">
                            <line x1="23" x2="23" y2="135" stroke="#C6E7F1" stroke-width="6"/>
                            <line x1="22.9997" y1="163.956" x2="24.9997" y2="300.956" stroke="#C6E7F1" stroke-width="6"/>
                            <circle cx="22.5" cy="150.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                            </g>
                            <defs>
                            <clipPath id="clip0_1_2">
                            <rect width="45" height="301" fill="white"/>
                            </clipPath>
                            </defs>
                        </svg>
                    </div>

                    <div className="paraen">
                    <p className="paragraph">
                        Every Nettuni course follows a specific structure, consisting of 8-12 chapters as its foundational blocks. Each chapter, in turn, comprises 6-12 subchapters.
                        <br/>
                        Within each subchapter, you'll encounter the primary course material, presented through a concise 10-20 minute lecture. This is complemented by a minimum of 15 problems that incrementally escalate in difficulty. Solutions to these problems are then provided.
                        <br/>
                        For the final set of problems, typically the most challenging ones, Nettuni offers video solutions. In these instances, the course teacher simplifies the solution through video format, enhancing comprehension for learners.
                    </p>
                    </div>
                </div>
            </div>

            <div className="use-5">

                <div className="use-header">
                    <h2 className="header-text">Problems and solutions</h2>
                </div>

                <div className="use-para">

                    <div className="use-para-svg">
                        <svg className="svg-to" viewBox="0 0 45 301" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <g clip-path="url(#clip0_1_2)">
                            <line x1="23" x2="23" y2="135" stroke="#C6E7F1" stroke-width="6"/>
                            <line x1="22.9997" y1="163.956" x2="24.9997" y2="300.956" stroke="#C6E7F1" stroke-width="6"/>
                            <circle cx="22.5" cy="150.5" r="12.5" stroke="#C6E7F1" stroke-width="10"/>
                            </g>
                            <defs>
                            <clipPath id="clip0_1_2">
                            <rect width="45" height="301" fill="white"/>
                            </clipPath>
                            </defs>
                        </svg>
                    </div>

                    <div className="paraen">
                        <p className="paragraph">
                            Learning is inherently challenging, and at Nettuni, we embrace this reality without any sugarcoating. 
                            The essence of mastering subjects such as mathematics, physics, chemistry, computer science, information technology, and engineering lies in the active engagement of problem-solving.
                            <br/>
                            The learning process involves comprehending a small portion of the material, testing understanding through solving easier problems, and gradually building confidence by tackling more complex challenges. This philosophy is embedded in our course structure, where each course is organized into chapters and subchapters. 
                            <br/>
                            Each subchapter is designed to offer a minimum of 15 problems and their corresponding solutions, fostering a hands-on and iterative approach to learning.
                        </p>
                    </div>
                </div>
            </div>

        </div>

    </div>
}